import React, { Component } from "react";
import "./SideMenuComponentStyle.css";

import Dotdotdot from "react-dotdotdot";

export class RecentComponent extends Component {
  constructor(props) {
    super(props);

    this.state = {
      active: ""
    };
  }

  componentDidMount(){
    if(this.props.index === 0 ){
      this.setState({
        active : "activeMenu"
      })
    }
  }

  render() {
    const { recent } = this.props;
    return (
      <>
        {recent ? (
          <div className={`recentList ${this.state.active}`}>
            <div className="details">
              {recent.album.name ? (
                <div className="title">
                  <Dotdotdot clamp={1}>{recent.album.name}</Dotdotdot>
                </div>
              ) : null}
              <div className="type">{recent.album.artist}</div>
            </div>
          </div>
        ) : null}
      </>
    );
  }
}


export default RecentComponent;
