import React, { Component } from "react";
import "./SideMenuComponentStyle.css";
import logo from "../../asset/spotify_logo.png";
import "../FontAwesome";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowCircleDown } from "@fortawesome/free-solid-svg-icons";
import { RecentComponent } from "./RecentComponent";

import axios from "axios";
import { Link } from "react-router-dom";

class SideMenuComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeIndex: 0,
      active: 1,
      isPlaying: ""
    };
  }

  changeSong = (id, index) => {
    if (index !== 0) {
      axios
        .get(
          `https://hailduong.github.io/lyduong/react-assignment/spotify-2/apis/album/${id}.json`
        )
        .then(response => {
          this.props.updateAlbum(response.data, id);
          this.setState({
            active: 3
          });
        })
        .catch(error => {
          console.log("Error", error);
        });
    } else {
      this.props.startPlay();
      this.setState({
        active: 3
      });
    }
  };

  sendActive = index => {
    this.setState({
      activeIndex: index
    });
  };

  componentWillReceiveProps(nextProps) {
    if (nextProps.playing === true || !nextProps.stopping) {
      this.setState({
        isPlaying: "playing"
      });
    } else {
      this.setState({
        isPlaying: ""
      })
    }
  }

  componentDidMount() {
    if (
      window.location.pathname === "/nhothspotify/albumdetails" ||
      window.location.pathname === "/nhothspotify/library"
    ) {
      this.changeActive("3");
    } else if (window.location.pathname === "/nhothspotify/search") {
      this.changeActive("2");
    }

    if (this.props.playing || !this.props.stopping) {
      this.setState({
        isPlaying: "playing"
      });
    } else {
      this.setState({
        isPlaying: ""
      })
    }
  }

  changeActive = index => {
    this.setState({
      active: parseInt(index)
    });
  };

  render() {

    const { all_data } = this.props.all_data;
    const layout = (
      <>
        <div className="menuComponent">
          {/* LOGO IMAGE */}
          <div className="logo">
            <Link to="/">
              <img src={logo} id="logo" alt="Loading Logo" />
            </Link>
          </div>

          {/* SIDE MENU */}
          <div className="menu">
            <ul>
              <Link to="/">
                <li
                  className={this.state.active === 1 ? "activeMenu" : null}
                  onClick={() => {
                    this.changeActive("1");
                  }}
                >
                  <FontAwesomeIcon icon="home" />
                  Home
                </li>
              </Link>

              <Link to="/search">
                <li
                  className={this.state.active === 2 ? "activeMenu" : null}
                  onClick={() => {
                    this.changeActive("2");
                  }}
                >
                  <FontAwesomeIcon icon="search" />
                  Search
                </li>
              </Link>

              <Link to="/library">
                <li
                  className={this.state.active === 3 ? "activeMenu" : null}
                  onClick={() => {
                    this.changeActive("3");
                  }}
                >
                  <FontAwesomeIcon icon="braille" />
                  Your Library
                </li>
              </Link>
            </ul>
          </div>
          <br />

          {/* RECENTLY PLAYED */}
          <div className="recent">RECENTLY PLAYED</div>

          <div className="sumList">
            {this.props.recentPlay.map((recent, index) => {
              return (
                <div
                  id="recent"
                  onClick={() => {
                    this.changeSong(recent.id, index);
                  }}
                  key={index}
                >
                  <Link to="/albumdetails">
                    <RecentComponent
                      index={index}
                      recent={recent}
                      type={all_data.user.recentlyPlayedType[recent.type]}
                    />
                  </Link>
                </div>
              );
            })}
          </div>

          <div className={`install ${this.state.isPlaying}`}>
            <a href="https://www.spotify.com/vn-en/download/windows/">
              <div className="installIcon">
                <FontAwesomeIcon icon={faArrowCircleDown} />
              </div>
              <div className="installWord">Install App</div>
            </a>
            <hr />

            <div className="account">
              <div className="avatar">
                <img src={all_data.user.avatar} alt="userAvatar" />
              </div>
              <div className="userName">{all_data.user.name}</div>
            </div>
          </div>
        </div>
      </>
    );

    return layout;
  }
}

export default SideMenuComponent
