import React from "react";
import Dotdotdot from "react-dotdotdot";

function OneAlbumSearch(props) {
  
  return (
    <div className="oneAlbumSearch" onClick={()=> {
      props.chooseAlbum(props.album);
      props.history.push("/albumdetails")
      }}>
      <img src={props.album.images[1].url} alt="albumImage" />
      <Dotdotdot clamp={2}>
        <div>{props.album.name}</div>
      </Dotdotdot>
    </div>
  )
}

export default OneAlbumSearch;
