import React from "react";
import PlaylistComponent from "../../PlaylistComponent/PlaylistComponent";
import PlaylistDetailsComponent from "../../PlaylistDetailsComponent/PlaylistDetailsComponent";
import { Col } from "reactstrap";

function AlbumComponent(props) {
  
  return (
    <React.Fragment>
      <Col xs="6" sm="4" className="loading">
        <PlaylistDetailsComponent {...props} />
      </Col>
      <Col xs="6" sm="6" className="loading">
        <PlaylistComponent {...props}/>
      </Col>
    </React.Fragment>
  );
}

export default AlbumComponent;
