import React, { Component } from "react";
import OneAlbum from "./OneAlbum";
import { Col, Row } from "reactstrap";
import axios from "axios";
import "./Library.css"
import {withRouter} from 'react-router-dom';

export class LibraryComponent extends Component {

  constructor(props) {
    super(props)

    this.state = {
      status: undefined,
      data: [],
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization:
          `Bearer ${this.props.token}`
      },
    }
  }

  componentDidMount() {
    axios
      .get(
        `https://api.spotify.com/v1/search?q=top&type=track,album&limit=6`,
        { headers: this.state.headers }
      )
      .then(res => {
        this.setState({
          status: true,
          data: res.data.albums.items
        });
      })
      .catch(error => {
        this.setState({
          tokenExpired: true
        });
      });
  }

  render() {
    const { all_data, status } = this.props.all_data;
    return (
      <>
        {status ? (
          <Col sm="10" className="libContent">
            <div className="category">Album</div>
            <Row>
              {this.state.data.map(album => 
                (
                  <OneAlbum
                    key={album.id}
                    album={album}
                    chooseAlbum={this.props.chooseAlbum}
                    history = {this.props.history}
                  />

                )
              )}
            </Row>

            {/* <Row>
              {this.state.data.map(album => ({
                <OneAlbum
                  chooseAlbum = {this.props.chooseAlbum}
                  // key={index}
                  // albumDetails={this.props.album}
                  album={album}
                  // index={index}
                  // click={this.click}
                />
              }))}
            </Row> */}
          </Col>
        ) : (
            <div>Loading...</div>
          )}

      </>
    );
  }
}

export default withRouter(LibraryComponent);
