import React, { Component } from "react";
import { Col } from "reactstrap";
import "./OneAlbum.css";
import axios from "axios";
import Dotdotdot from "react-dotdotdot";

export class OneAlbum extends Component {
  constructor(props) {
    super(props)
  
    this.state = {
       artist : "",
       clamp : 1
    }
  }

  changeClamp = ()=>{
    this.setState({
      clamp : 3
    })
  }

  defaultClamp = ()=>{
    this.setState({
      clamp : 1
    })
  }
  
  render() {
    console.log(this.props)
    const artist = this.props.album ? this.props.album.artists[0].name : ""
    var styles = {
      backgroundImage: `url(${this.props.album.images[0].url})`,
      backgroundSize: "cover",
      backgroundPosition: "center center",
      width: "100%",
      height: "250px",
      borderRadius: "5px",
      boxShadow: "5px 5px 20px rgb(48, 48, 48)"
    };
    return (
      
        <Col xs="4" sm="3">
          <div
            className="oneAlbum"
            onMouseOver={this.changeClamp} onMouseOut={this.defaultClamp}
            onClick={ ()=>{
              this.props.chooseAlbum(this.props.album);
              this.props.history.push("/albumdetails")
            }
            }
          >
            <div style={styles} />
            <div className="albumTitle"  >
              <Dotdotdot clamp={this.state.clamp} className="albumName">
              {this.props.album.name}
              </Dotdotdot>
              
              <em>{artist}</em>
            </div>
          </div>
        </Col>
      
    );
  }
}
export default OneAlbum;
