import axios from "axios";
import React from "react";
import SpotifyWebApi from "spotify-web-api-js";
import { withRouter } from 'react-router-dom'

const URL_USER =
  "https://raw.githubusercontent.com/hailduong/lyduong/master/react-assignment/spotify/apis/userInfo.json";
const URL_ALLALBUM =
  "https://hailduong.github.io/lyduong/react-assignment/spotify-2/apis/yourLibrary.json";

const URL_FIRST_ALBUM =
  "https://hailduong.github.io/lyduong/react-assignment/spotify-2/apis/album/1.json";
let audio = undefined;
const get_data = ChildComponent => {
  const spotifyApi = new SpotifyWebApi();
  class WrappedComponent extends React.Component {
    constructor(props) {
      super(props);

      const params = this.getHashParams();
      //get access token
      const token = params.access_token;
      spotifyApi.setAccessToken(token);
      if (token === undefined) {
        window.location = "https://accounts.spotify.com/authorize?client_id=08dfa6ac8c444d0198d37efb64b182ef&response_type=token&Foauth_callback%2F&scope=streaming user-read-playback-state user-read-currently-playing user-modify-playback-state&redirect_uri=https:%2F%2Feinstruong.github.io/nhothspotify";
      }

      this.state = {
        all_data: {},
        status: false,
        album: [],
        playing: false,
        stopping: true,
        recentPlay: [],
        repeat: false,
        TOKEN: token || "",
        playingData: {},
        playingTrack: [],
        preview_url: ""
      };
    }

    playAudio = (preview_url) => {
      try {
        audio.pause();
        audio.currentTime = 0;
      } catch{ };
      audio = new Audio(preview_url);
      audio.play();
    }

    getHashParams() {
      let hashParams = {};
      let e,
        r = /([^&;=]+)=?([^&;]*)/g,
        q = window.location.hash.substring(1);
      e = r.exec(q);
      while (e) {
        hashParams[e[1]] = decodeURIComponent(e[2]);
        e = r.exec(q);
      }
      return hashParams;
    }

    componentDidMount() {
      let user = axios
        .get(URL_USER)
        .then(res => res.data)
        .catch(error => console.log("Error User", error));
      let all_album = axios
        .get(URL_ALLALBUM)
        .then(res => res.data)
        .catch(error => console.log("Error All Album", error));

      let first_album = axios
        .get(URL_FIRST_ALBUM)
        .then(res => res.data)
        .catch(error => console.log("Error First Album", error));

      axios
        .all([user, all_album, first_album])
        .then(res => {
          this.setState({
            all_data: {
              user: res[0],
              all_album: res[1]
            },
            album: res[2],
            status: true
          });
        })
        .catch(error => {
          console.log("Promise All Error", error);
        });
    }

    chooseAlbum = (album) => {

      this.state.recentPlay.unshift({ id: album.id, album: album });

      if (this.state.recentPlay.length > 4) {
        this.state.recentPlay.splice(-1, 1);
      }

      const headers = {
        "Content-Type": "application/json",
        "Accept": "application/json",
        "Authorization":
          `Bearer ${this.state.TOKEN}`
      }

      axios.get(`https://api.spotify.com/v1/albums/${album.id}/tracks`, { headers: headers }).then(res => {
      
        this.setState({
          ...this.state,
          playingData: {
            image: album.images[0].url,
            playingTrackName: res.data.items[0].name,
            playingTrackId: res.data.items[0].id,
            artist: album.artists[0].name,
            name: album.name,
            releaseDate: album.release_date
          },
          playingTrack: res.data.items,
          recentPlay: [...this.state.recentPlay],
          playing: true,
          stopping: false
        }, () => {
          this.playAudio(res.data.items[0].preview_url);
        })
      })
    }

    chooseSong = (track) => {
      this.playAudio(track.preview_url);
      this.setState(prevState => ({
        ...this.state,
        playingData: {
          ...this.state.playingData,
          playingTrackId: track.id,
          image : track.album ? track.album.images[0].url : prevState.playingData.image,
          playingTrackName: track.name,
          artist : track.artists[0].name
        },
        recentPlay: [...this.state.recentPlay],
        playing: true,
        stopping: false
      }))
    }

    control_play = () => {
      audio.play();
      this.setState({
        ...this.state,
        playing: true,
        stopping : false
      });
    };

    control_pause = () => {
      audio.pause();
      this.setState({
        ...this.state,
        playing: false,
        stopping : false
      });
    };

    control_stop = () => {
      audio.pause();
      audio.currentTime = 0;
      this.setState(prevState => ({
        ...this.state,
        playing: false,
        stopping: true
      }));
    };

    control_start = () => {
      audio.play();
      this.setState(prevState => ({
        ...this.state,
        playing: true,
        stopping: false
      }));
    };

    choose_song = track => {
      this.setState({
        ...this.state,
        all_data: {
          ...this.state.all_data,
          user: {
            ...this.state.all_data.user,
            playing: track
          }
        },
        playing: true,
        stopping: false
      });
    };

    control_repeat = () => {
      this.setState(prevState => ({
        ...this.state,
        repeat: !prevState.repeat
      }));
      console.log(this.state);
    };

    render() {
      return (
        <ChildComponent
          all_data={this.state}
          playing={this.state.playing}
          stopping={this.state.stopping}
          updateAlbum={this.updateAlbum}
          control_play={this.control_play}
          control_pause={this.control_pause}
          control_stop={this.control_stop}
          control_start={this.control_start}
          // choose_song={this.choose_song}
          // control_repeat={this.control_repeat}
          recentPlay={this.state.recentPlay}
          token={this.state.TOKEN}
          chooseAlbum={this.chooseAlbum}
          playingData={this.state.playingData}
          playingTrack={this.state.playingTrack}
          chooseSong={this.chooseSong}
        />
      );
    }
  }
  return WrappedComponent;
};

export default get_data;
