import React from "react";
import { Row, Col } from "reactstrap";
import OneAlbumSearch from "../TopComponent/OneAlbumSearch";

function AlbumSearchComponent(props) {
  const { items } = props.data;
  return (
    items.length > 0 && (
      <>
        <div className="searchTitle">ALBUMS</div>
        <Row>
          <Col sm="12" xs="main">
            <Row>
              {items.length > 0 ? (
                items.map(album => (
                  <React.Fragment key={album.id}>
                    <Col sm="2">
                      <OneAlbumSearch album={album} chooseAlbum = {props.chooseAlbum} chooseSong={props.chooseSong} history={props.history} />
                    </Col>
                  </React.Fragment>
                ))
              ) : (
                <div className="Error">No Any Album</div>
              )}
            </Row>
          </Col>
        </Row>
      </>
    )
  );
}

export default AlbumSearchComponent;
