import React from "react";
import { Row, Col } from "reactstrap";
import OneTrackSearch from "../TopComponent/OneTrackSearch";

function SongComponent(props) {
  const { items } = props.data;
  return (
    items.length > 0 && (
      <>
        <div className="searchTitle">TRACKS</div>
        <Row>
          <Col sm="6" xs="main">
            <Row>
              {items.length > 0 ? (
                items.slice(0, 9).map(track => (
                  <React.Fragment key={track.id}>
                    <Col sm="12">
                      <OneTrackSearch track={track} chooseSong={props.chooseSong} />
                    </Col>
                  </React.Fragment>
                ))
              ) : (
                <div className="Error">No Any Track</div>
              )}
            </Row>
          </Col>

          <Col sm="6">
            <Row>
              {items.length > 0 ? (
                items.slice(10, 19).map(track => (
                  <React.Fragment key={track.id}>
                    <Col sm="12">
                      <OneTrackSearch track={track} chooseSong={props.chooseSong} />
                    </Col>
                  </React.Fragment>
                ))
              ) : (
                <div className="Error">No Any Track</div>
              )}
            </Row>
          </Col>
        </Row>
      </>
    )
  );
}

export default SongComponent;
