import React, { Component } from "react";
import "./PlaylistDetailsComponentStyle.css";
import { Button } from "reactstrap";

class PlaylistDetailsComponent extends Component {
  constructor(props) {
    super(props);
    // this.isPlay = this.isPlay.bind(this);
    // this.stop = this.stop.bind(this);
    // this.play = this.play.bind(this);
  }

  // stop(){
  //   const { album } = this.props;
  //   this.props.chooseSong({ album: album.name, track: album.tracks[0].number });
  // };

  // play(){
  //   const {album } = this.props;
  //   this.props.chooseSong({ album: album.name, track: album.tracks[0].number });
  //   this.props.startPlay();
  // };

  render() {
    
    const {playingData, playingTrack} = this.props;
    const {all_data} = this.props;
    
    return (
      
      <>
        {
          all_data.status ? 
          <div className="detailsComponent">
          <div className="image">
            <img src={playingData.image} alt="Loading Cover" />
          </div>

          <div className="titleCover">{playingData.name}</div>

          <div className="singer">{playingData.artist}</div>
          
          { ( !all_data.playing && all_data.stopping) ? (
            <Button color="success" onClick={this.props.control_start}>
              PLAY
            </Button>
          ) : (
            <Button color="danger" onClick={this.props.control_stop}>
              STOP
            </Button>
          )}

          <div className="totalSong">{`${playingData.releaseDate} . ${
            playingTrack.length
          } SONG`}</div>
          <div className="controlCover">
            <div className="controlType">REMOVE FROM YOUR LIBRARY</div>
            <div className="moreControl">...</div>
          </div>
        </div> : null
        }
          
      </>
    );
  }
}

export default PlaylistDetailsComponent;
