import React from 'react'

function OneTrackSearch(props) {
  return (
    <div className="oneTrackSearch oneAlbumSearch" onClick={()=>{
      props.chooseSong(props.track);
    }}>
      <div>{props.track.name}</div>
      <div>
        {props.track.artists[0].name}
      </div>
    </div>
  )
}

export default OneTrackSearch
