import React, { Component } from "react";
import { Row, Col } from "reactstrap";
import OneAlbumSearch from "./OneAlbumSearch";
import OneTrackSearch from "./OneTrackSearch";

class TopComponent extends Component {
  
  render() {
    const { albums, tracks } = this.props.data;
    return albums.items.length > 0 || tracks.items.length > 0 ? (
      <Row>
        <Col sm="7" xs="main">
          <div className="searchTitle">TOP 5 ALBUMS</div>
          <Row>
            {albums.items.length > 0 && (
              albums.items.slice(0, 5).map(album => (
                <React.Fragment key={album.id}>
                  <Col sm="4">
                    <OneAlbumSearch album={album} chooseAlbum = {this.props.chooseAlbum} history={this.props.history}/>
                  </Col>
                </React.Fragment>
              ))
            )}
          </Row>
        </Col>

        <Col sm="5">
          <div className="searchTitle">TOP 5 TRACKS</div>
          <Row>
            {tracks.items.length > 0 ? (
              tracks.items.slice(0, 5).map(track => (
                <React.Fragment key={track.id}>
                  <Col sm="12">
                    <OneTrackSearch track={track} chooseSong={this.props.chooseSong} />
                  </Col>
                </React.Fragment>
              ))
            ) : (
              <div className="Error">No Any Track</div>
            )}
          </Row>
        </Col>
      </Row>
    ) : (
      <div className="Error">No Anything Found</div>
    );
  }
}

export default TopComponent;
